export const Pangolicons = {
	icons: {
		airplay,alignCenter,alignJustify,alignLeft,alignRight,archive,arrowDown,arrowLeft,arrowLeftDown,arrowLeftUp,arrowRight,arrowRightDown,arrowRightUp,arrowUp,barChart,barChartAlternative,barChartPositive,batteryCharged,batteryEmpty,bell,bellOff,bellRinging,bluetooth,bluetoothConnected,bluetoothOff,bluetoothSearching,bold,bookClosed,bookOpen,bookmark,box,calendar,camera,cameraOff,castToMedia,checkmark,checkmarkCircle,chevronDown,chevronDownUp,chevronFirst,chevronLast,chevronLeft,chevronLeftRight,chevronRight,chevronUp,chevronUpDown,chevronrightLeft,chevronsDown,chevronsLeft,chevronsRight,chevronsUp,circle,circleArrowDown,circleArrowLeft,circleArrowLeftDown,circleArrowLeftUp,circleArrowRight,circleArrowRightDown,circleArrowRightUp,circleArrowUp,circleMinus,circlePlus,circleX,clipboard,clipboardNotes,cloud,cloudDrizzle,cloudFog,cloudHail,cloudLightning,cloudMoon,cloudRain,cloudSnow,cloudStorm,cloudSun,clouds,code,codepen,convertToLink,convertToOrderedList,convertToUnorderedList,convertToUnstyledList,cookie,cornerArrowDownLeft,cornerArrowDownRight,cornerArrowLeftDown,cornerArrowLeftUp,cornerArrowRightDown,cornerArrowRightUp,cornerArrowUpLeft,cornerArrowUpRight,creditcard,crown,database,delimitor,display,download,dragIndicator,edit,envelope,externalLink,eyeClosed,eyeOpen,eyeStriked,facebook,fader,fastForward,file,filter,fnBackspace,fnCaps,fnCommand,fnControl,fnEscape,fnOption,fnReturn,fnShift,fnSpacebar,fnTab,folder2,folder,folderMinus,folderPlus,fork,form,frown,fullscreenEnter,fullscreenExit,gear,ghost,github,guitar,haze,headingDecrease,headingIncrease,heart,hexagon,horizontalMore,icecream,image,infoCircle,infoSquare,instagram,italic,jellyfish,key,layers,layout,layoutColumns,layoutGrid,layoutHorizontal,layoutList,layoutMixed,layoutVertical,library,list,location,lock,login,logoutLeft,logoutRight,marker,menu,microphone,microphoneOff,minus,moonRough,moonStylized,move,moveDiagonal,moveDiagonalMirrored,moveHorizontal,moveVertical,mug,music,neutral,pangolin,parcel,pause,phone,phoneCall,phoneIncoming,phoneMissed,phoneOff,phoneOutgoing,pieChart,play,plus,pothos,power,rainbow,refresh,refreshCCW,refreshCW,repeat,resizeImage,rewind,robot,search,server,settings,shield,shuffle,sidebar,skipBack,skipForward,sliders,smartphone,smile,snail,snowflake,snpachat,spoon,square,squareMinus,squarePlus,squareX,star,starHalf,strikethrough,sunBig,sunSmall,sword,tablet,terminal,text,title,trashcanEmpty,trashcanFilled,trello,triangle,triangleInverted,twitch,twitter,umbrella,underline,unlock,user,userAdd,userMultiple,userRemove,verticalMore,voicemail,volumeHigh,volumeLow,volumeMedium,volumeMute,webcam,wifi,wifiOff,wind,wrench,x,youtube,zoomIn,zoomOut
	},

	/**
	 *  @method
	 *	@description static method to replace a element with a icon, if that element has a pangolicons attribute.
	 *
	 * 	@param { Object } param0 - the object passed to the replace method with the element property
	 * 	@param { HTMLElement } param0.element - the HTMLElement to replace.
	 *
	 * 	@returns { SVGElement } the created svgElement with the icon
	 */

	replace({ element }) {
		if (element === undefined || !(element instanceof HTMLElement)) {
			console.error(
				`Pangolicons: ${element} is either undefined or no HTMLElement.`
			);
		}

		if (typeof window === 'undefined' || typeof document === 'undefined') {
			console.error(
				`Pangolicons: The 'replaceAll' & 'replace' methods will only work in a browser enviroment.`
			);
			return;
		}

		const iconName = element.getAttribute('pangolicons');

		if (iconName === undefined) {
			console.log(
				`Pangolicons: element to replace does not have a 'pangolicons' attribute.`
			);
			return;
		}

		const attributes = {};

		Array.from(element.attributes).forEach((attribute) => {
			attributes[attribute.name] = attribute.value;
		});

		const icon = Pangolicons.icons[iconName].toSvg(attributes);

		element.replaceWith(icon);

		return icon;
	},

	/**
	 * @description method to replace all icons with a pangolicons attribute on the page.
	 */

	replaceAll() {
		if (typeof window === 'undefined' || typeof document === 'undefined') {
			console.error(
				`Pangolicons: The 'replaceAll' & 'replace' methods will only work in a browser enviroment.`
			);
			return;
		}

		const icons = document.querySelectorAll('i[pangolicons]');
		icons.forEach((element) => this.replace({ element }));
	},

	/**
	 *	@description method to search the Pangolicons.icons object for all icons which tags (and in extension name)
	 * matches the defined searchstring.
	 *
	 * 	@param { Object } param0 - the object passed to the replace method with the element property
	 * 	@param { String } param0.searchString - the String to search for
	 *
	 * 	@returns { Icon[] } an Array containing all icons found with the matching tags
	 */

	search({ searchString }) {
		if (searchString !== 'x' && searchString.length < 3) {
			return;
		}

		// convert the searchstring to lowercase

		searchString = searchString.toLowerCase();

		// helper method to match tags to the searchstring

		const matchTags = (searchString, tags) =>
			tags
				.map((elem) => elem.toLowerCase())
				.some((tag) => tag.includes(searchString));

		// itterate over the icons

		return Object.values(Pangolicons.icons)
			.map((icon) => {
				if (matchTags(searchString, icon.tags)) {
					return icon;
				}
			})
			.filter((elem) => elem != undefined);
	},
};

globalThis.Pangolicons = Pangolicons;
